<template>
  <div ref="container">
    <canvas ref="canvas" id="supernova-canvas"></canvas>
    <div
      id="next-supernova-button"
      v-on:click="addSupernova()"
      v-bind:class="{ disabled: buttonDisabled }"
    >
      <div id="button-text-1">{{ nextSupernovaDisplay[0] }}</div>
      <div id="button-text-2">{{ nextSupernovaDisplay[1] }}</div>
    </div>
    <div id="supernova_counter">
      <div id="you-have">你已获得</div>
      <div id="you-have-en">You Have Became</div>
      <div id="supernova-number">
        {{ displayedSupernovas }}
      </div>
      <div id="supernova-number-bg" v-bind:class="{ fading: inTextAnimation }">
        {{ displayedSupernovas }}
      </div>
      <div id="supernovas">超新星</div>
      <div id="supernovas-en">Supernovas</div>
    </div>
  </div>
</template>

<style scoped>
#supernova-canvas {
  width: 100%;
  height: 100%;
}

#next-supernova-button {
  position: absolute;
  top: 70%;
  width: 400px;
  height: 100px;
  font-size: 45px;
  font-family: Garamond, "Times New Roman", sans-serif;
  color: #ccc;
  text-align: center;
  background: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  text-shadow: 2px 2px 5px #0006;
}

#next-supernova-button.disabled {
  cursor: default;
}

#next-supernova-button:not(.disabled):hover {
  text-shadow: 2px 2px 5px #000e;
}

#next-supernova-button::after {
  display: flex;
  content: "";
  width: 400px;
  height: 100px;
  position: absolute;
  top: 10px;
  background: radial-gradient(#8e44ad, transparent 45%);
  opacity: 0;
  transform: scale(0.1);
  transition-duration: 300ms;
}

#next-supernova-button.disabled::after {
  background: radial-gradient(#000000, transparent 45%);
  transform: scale(1);
  opacity: 0.8;
  transition-duration: 300ms;
}

#next-supernova-button:not(.disabled):hover::after {
  opacity: 0.3;
  transform: scale(1);
  transition-duration: 300ms;
}

#button-text-1 {
  position: relative;
  left: -28px;
  bottom: -15px;
  color: #ccc;
  z-index: 1;
  transition-duration: 300ms;
}

#next-supernova-button.disabled > #button-text-1,
#next-supernova-button.disabled > #button-text-2 {
  color: #555;
}

#next-supernova-button:not(.disabled):hover > #button-text-1 {
  transform: translateX(-5px);
  transition-duration: 300ms;
}

#button-text-2 {
  position: relative;
  left: 20px;
  top: -14px;
  color: #74b9ffcc;
  font-size: 40px;
  z-index: 2;
  transition-duration: 300ms;
}

#next-supernova-button:not(.disabled):hover > #button-text-2 {
  transform: translateX(3px);
  transition-duration: 300ms;
}

#supernova_counter {
  position: absolute;
  width: 400px;
  height: 300px;
}

#you-have {
  position: absolute;
  left: 16px;
  top: 105px;
  font-family: "SimSun", "Microsoft YaHei", "PingFang SC", "san-serif";
  font-size: 46px;
  color: #ccc;
  text-shadow: 2px 2px 5px #000e;
}

#supernovas {
  position: absolute;
  left: 237px;
  top: 172px;
  font-family: "SimSun", "Microsoft YaHei", "PingFang SC", "san-serif";
  font-size: 50px;
  color: #74b9ffcc;
  text-shadow: 2px 2px 5px #000e;
}

#you-have-en {
  position: absolute;
  left: 16px;
  top: 137px;
  font-family: Garamond, "Times New Roman", sans-serif;
  font-size: 26px;
  color: #7f8c8d66;
  text-shadow: 2px 2px 5px #000e;
  z-index: -1;
}

#supernovas-en {
  position: absolute;
  left: 238px;
  top: 203px;
  font-family: Garamond, "Times New Roman", sans-serif;
  font-size: 33px;
  color: #2980b966;
  text-shadow: 2px 2px 5px #000e;
  z-index: -1;
}

#supernova-number {
  position: absolute;
  left: 199px;
  top: 59px;
  font-family: Garamond, "Times New Roman", sans-serif;
  font-size: 163px;
  color: #ccc;
  text-shadow: -4px 4px 0px #555f;
  z-index: 10;
}

#supernova-number-bg {
  position: absolute;
  left: 199px;
  top: 59px;
  font-family: Garamond, "Times New Roman", sans-serif;
  font-size: 163px;
  color: #ccc;
  text-shadow: 0px 0px 30px #fff, 0px 0px 30px #fff, 0px 0px 30px #fff,
    0px 0px 30px #fff;
  z-index: 11;
  opacity: 0.2;
}

.fading {
  animation-name: number-glow-anim;
  animation-duration: 0.8s;
  animation-timing-function: ease-in-out;
}

@keyframes number-glow-anim {
  0% {
    opacity: 0.2;
  }
  15% {
    opacity: 0.8;
  }
  50% {
    opacity: 1;
  }
  80% {
    opacity: 0.9;
  }
  100% {
    opacity: 0.2;
  }
}
</style>

<script>
class SupernovaShape {
  constructor(id, color) {
    this.id = id;
    this.vortexs = 0;
    this.angles = [];
    this.speed = 0;
    this.long_axis = 0;
    this.short_axis = 0;
    this.color = color;
  }

  setAngles(angles) {
    this.vortexs = angles.length;
    this.angles = angles;
  }

  setShapeParams(long_axis, short_axis, speed) {
    this.long_axis = long_axis;
    this.short_axis = short_axis;
    this.speed = speed;
  }

  setPivotParams(main_pivot, scale_per_id, pivot_offset, pivot_scale) {
    let l = (main_pivot[0] ** 2 + main_pivot[1] ** 2) ** 0.5;
    this.main_pivot = [main_pivot[0] / l, main_pivot[1] / l];

    this.scale_per_id = scale_per_id;
    this.pivot_offset = pivot_offset;
    this.pivot_scale = pivot_scale;
  }

  getVortexes(canvasW, canvasH, progress) {
    let vortexes = [];
    for (let i = 0; i < this.vortexs; i++) {
      let axis = i % 2 == 0 ? this.long_axis : this.short_axis;
      let angle = this.angles[i];
      vortexes.push([Math.sin(angle) * axis, Math.cos(angle) * axis]);
    }
    this.applyPivotTransform(canvasW, canvasH, vortexes, progress);
    return vortexes;
  }

  update(diff) {
    let angle_diff = this.speed * diff;
    for (let i in this.angles) {
      // might add randomized speed for each vortex but not for now
      this.angles[i] += angle_diff;
    }
  }

  applyPivotTransform(canvasW, canvasH, vortexes, progress) {
    let progress_scale = 1.0 * (1 - progress) ** 2 + 1;
    let scale = progress_scale * this.scale_per_id ** this.id;
    for (let v in vortexes) {
      let vortex = vortexes[v];
      let x = vortex[0],
        y = vortex[1];
      x = x * this.pivot_scale;
      let p_x = this.main_pivot[0] * x - this.main_pivot[1] * y;
      let p_y = this.main_pivot[1] * x + this.main_pivot[0] * y;

      let off_x =
        canvasW + (this.id - 6) * this.pivot_offset * this.main_pivot[0];
      let off_y =
        canvasH + (this.id - 6) * this.pivot_offset * this.main_pivot[1];

      vortex[0] = p_x * scale + off_x;
      vortex[1] = p_y * scale + off_y;
      vortexes[v] = vortex;
    }
    return vortexes;
  }
}

export default {
  data() {
    return {
      supernovas: 0,
      supernova_shapes: [],
      fadeInCounter: -10000,
    };
  },
  created() {
    this.MAX_SUPERNOVA = 7;
    this.STAR_LONG_AXIS = 140;
    this.STAR_SHORT_AXIS = 60;
    this.POLYGON_MIN_VERTS = 6;
    this.POLYGON_VERTS_RANGE = 3;
    this.NOVA_SCALE = 0.8;
    this.NOVA_PIVOT_VEC = [-1, -1];
    this.NOVA_PIVOT_OFFSET = 20;
    this.NOVA_PIVOT_SCALE = 0.5;
    this.SPIN_SPEED = (2 * Math.PI) / 10000;
    this.SPIN_SPEED_RND = 0.2;
    this.FADE_IN_TIME = 2000;
    this.FADE_SCALE = 3.0;
    this.COLORS = [
      "#8e44ad",
      "#9b59b6",
      "#2980b9",
      "#3498db",
      "#27ae60",
      "#2ecc71",
      "#95a5a6",
    ];
  },
  mounted() {
    this.initCanvas();
    window.onresize = () => {
      this.initCanvas();
    };
    requestAnimationFrame(this.animate);
  },
  computed: {
    nextSupernovaDisplay() {
      if (this.supernovas == this.MAX_SUPERNOVA) {
        return ["Supernova", "Hardcapped"];
      }
      if (this.inAnimation) {
        return ["Supernova", "Inprogress"];
      }
      return ["Increase", "Supernova"];
    },
    buttonDisabled() {
      return this.inAnimation || this.supernovas == this.MAX_SUPERNOVA;
    },
    inAnimation() {
      return this.fadeInCounter > -800;
    },
    inTextAnimation() {
      return this.fadeInCounter > -800 && this.fadeInCounter < 0;
    },
    displayedSupernovas() {
      if (this.fadeInCounter > 0) {
        return this.supernovas - 1;
      }
      return this.supernovas;
    },
  },
  methods: {
    initCanvas() {
      this.canvas = this.$refs.canvas;
      this.ctx = this.canvas.getContext("2d");
      let container = this.$refs.container;
      this.canvas.width = container.offsetWidth;
      this.canvas.height = container.offsetHeight;
    },
    addSupernova() {
      if (this.inAnimation) {
        return;
      }
      let supernova_shape = new SupernovaShape(
        this.supernovas,
        this.COLORS[this.supernovas]
      );
      this.supernovas += 1;

      let points =
        Math.floor(Math.random() * this.POLYGON_VERTS_RANGE) +
        this.POLYGON_MIN_VERTS;
      let angles = [];
      for (let i = 0; i < points; i++) {
        let angle1 = (2 * i * Math.PI) / points;
        let angle2 = ((2 * i + 1) * Math.PI) / points;
        angles.push(angle1);
        angles.push(angle2);
      }

      supernova_shape.setAngles(angles);
      supernova_shape.setPivotParams(
        this.NOVA_PIVOT_VEC,
        this.NOVA_SCALE,
        this.NOVA_PIVOT_OFFSET,
        this.NOVA_PIVOT_SCALE
      );
      let speed_mul = (Math.random() - 0.5) * this.SPIN_SPEED_RND * 2;
      supernova_shape.setShapeParams(
        this.STAR_LONG_AXIS,
        this.STAR_SHORT_AXIS,
        (speed_mul + 1) * this.SPIN_SPEED
      );

      this.supernova_shapes.push(supernova_shape);
      this.fadeInCounter = this.FADE_IN_TIME;
    },
    animate(timestamp) {
      if (this.last_timestamp == 0) {
        this.last_timestamp = timestamp;
        requestAnimationFrame(this.animate);
        return;
      }
      this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
      let w = this.canvas.width * 0.45;
      let h = this.canvas.height * 0.4;

      let diff = timestamp - this.last_timestamp;
      for (let i in this.supernova_shapes) {
        let shape = this.supernova_shapes[i];
        let color = shape.color;

        let update_progress = 1;
        if (this.fadeInCounter > 0 && i == this.supernovas - 1) {
          update_progress = 1 - this.fadeInCounter / this.FADE_IN_TIME;
        }
        shape.update(diff);
        let vortexes = shape.getVortexes(w, h, update_progress);
        this.ctx.beginPath();
        this.ctx.globalAlpha = update_progress;
        this.ctx.moveTo(vortexes[0][0], vortexes[0][1]);
        for (let v in vortexes) {
          let vortex = vortexes[v];
          this.ctx.lineTo(vortex[0], vortex[1]);
        }
        this.ctx.fillStyle = color;
        this.ctx.fill();
      }

      this.last_timestamp = timestamp;
      this.fadeInCounter -= diff; // still useful under 0
      requestAnimationFrame(this.animate);
    },
  },
};
</script>
