import { createRouter, createWebHashHistory } from "vue-router";
import HomeView from "../views/HomeView";
import AboutView from "../views/AboutView";
import SupernovaView from "../views/SupernovaView";
import GlyphsView from "../views/GlyphsView";
import SpiralView from "../views/SpiralView";
import SteleView from "../views/SteleView";

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/about",
    name: "about",
    component: AboutView,
  },
  {
    path: "/become_supernova",
    name: "supernova",
    component: SupernovaView,
  },
  {
    path: "/stele_trials",
    name: "stele",
    component: SteleView,
  },
  {
    path: "/glyphs_show",
    name: "glyphs",
    component: GlyphsView,
  },
  {
    path: "/dual_heaven",
    name: "spiral",
    component: SpiralView,
  },
];

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes,
});

export default router;
